








































































import Vue from 'vue'
import { Form } from 'element-ui'
import { getAllSpaces, saveOrderUpdateAdvertSpace } from '@/services/advert-space'

export default Vue.extend({
  name: 'resourceCategoryList',
  data () {
    return {
      list: [],
      loading: false,
      dialogTitle: '添加分类',
      dialogVisible: false,
      advertform: {},
      rules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'change' }
        ]
      }
    }
  },
  mounted () {
    this.getAdvertSpacelist()
  },
  methods: {
    async getAdvertSpacelist () {
      this.loading = true
      const { data } = await getAllSpaces()
      console.log(data)

      this.list = data.content
      this.loading = false
    },
    edit (item: any) {
      this.dialogTitle = '编辑'
      this.dialogVisible = true
      this.advertform = JSON.parse(JSON.stringify(item))
    },
    add () {
      this.dialogTitle = '添加分类'
      this.dialogVisible = true
    },
    delete (item: any) {
      console.log(item)
    },
    handleSave () {
      (this.$refs.advertform as Form).validate(async (valid) => {
        if (valid) {
          const { data } = await saveOrderUpdateAdvertSpace(this.advertform)
          if (data.success === true) {
            this.$message({
              type: 'success',
              message: '操作成功!'
            })
            this.advertform = {}
            this.getAdvertSpacelist()
            this.dialogVisible = false
          }
        }
      })
    }

  }
})
